import {Component, OnInit} from '@angular/core';
import {NotificationService} from '../../service/notification.service';
import {SettingsHelper} from '../../helper/settings.helper';
import {NewsService} from '../../service/news.service';
import {LessonService} from '../../service/lesson.service';
import {VoucherService} from '../../service/voucher.service';
import {UserStore} from '../../store/user.store';
import * as Parse from 'parse';
import {IUser} from '../../model/user.model';
import {VoucherBrandService} from '../../service/voucherBrand.service';
import ChartStore from '../../store/chart.store';
import {VoucherRequestService} from '../../service/voucher-request.service';
import {SoldService} from "../../service/sold.service";

@Component({
  selector: 'app-tabs',
  templateUrl: './tabs.page.html',
  styleUrls: ['./tabs.page.scss'],
})
export class TabsPage implements OnInit {

  isHome = false;
  isNotification = false;
  isVoucher = false;
  isProfile = false;
  isTraining = false;

  constructor(private voucherRequestService: VoucherRequestService, private chartStore: ChartStore, private voucherService: VoucherService, private voucherBrandService: VoucherBrandService, private userStore: UserStore, private notificationService: NotificationService, private settingsHelper: SettingsHelper, private newsService: NewsService, private lessonService: LessonService, private soldService: SoldService) {
  }


  resetAllIcons() {
    this.isHome = false;
    this.isNotification = false;
    this.isVoucher = false;
    this.isProfile = false;
    this.isTraining = false;

  }

  async changeTab(event) {
    console.log('Got event data: ', event);
    this.resetAllIcons();
    if (event.tab === 'notification') {
      this.notificationService.getAllNotifications();
      this.isNotification = true;
    } else if (event.tab === 'home') {
      this.isHome = true;
      this.settingsHelper.loadSettings();
      Parse.User.current().fetch().then(r => {
        this.userStore.setUser(Parse.User.current().toJSON() as unknown as IUser);
      });
      this.newsService.getHomeNews();
    } else if (event.tab === 'voucher') {
      this.isVoucher = true;
      this.voucherBrandService.getAllVoucherBrands();
      this.voucherService.getAllVouchers();
      this.chartStore.updateChart();
      this.voucherRequestService.getAllVoucherRequests();
      this.soldService.getAllSold();

    } else if (event.tab === 'profile') {
      this.isProfile = true;
    } else {
      this.isTraining = true;
      this.lessonService.getAllLessons();

    }
  }

  ngOnInit() {
  }

}
