import { Injectable } from '@angular/core';
import * as Parse from 'parse';
import VoucherStore from '../store/voucher.store';
import {ParseHelper} from '../helper/parse.helper';
import {IVoucher, IVoucherBrand, IVoucherRequest, IVoucherRequestParse} from '../model/voucher.model';
import {UserStore} from "../store/user.store";
import {PaginationHelper} from "../helper/pagination.helper";
import VoucherBrandStore from "../store/voucherBrand.store";

@Injectable({
  providedIn: 'root'
})
export class VoucherBrandService {

  constructor(private voucherBrandStore: VoucherBrandStore) { }

  async getAllVoucherBrands() {
    try {
      const query = new Parse.Query('Voucher_Brand');
      const results: any[] = await query.findAll();
      const vouchers: IVoucherBrand[] = ParseHelper.arrayToJSON(results);
      console.log('Got voucher brands: ', vouchers);
      for(let el of vouchers){
        if(!await PaginationHelper.checkExisting(el,this.voucherBrandStore.voucherBrands)){
          this.voucherBrandStore.addVoucherBrand(el);
        }
      }
    } catch (e) {
      console.log(e);
      return 'Cant get vouchers';
    }
  }
}
